:root {
  --moderate-cyan: hsl(176, 50%, 47%);
  --dark-cyan: hsl(176, 72%, 28%);
  --black: hsl(0, 0%, 0%);
  --dark-gray: hsl(0, 0%, 48%);
  --gray: hsl(0, 0%, 90%);
  --light-gray: hsl(0, 0%, 95%);
}

:root {
  --font-commissioner: "Commissioner", sans-serif;
  --font-bold: 700;
  --font-regular: 500;
  --font-light: 400;
}

body {
  font-family: var(--font-commissioner);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-commissioner);
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

h2 {
  margin-bottom: 0;
}

p {
  margin-top: 5px;
  opacity: 50%;
  font-weight: var(--font-regular);
  font-size: 1.1rem;
  line-height: 1.9rem;
}

button {
  border: none;
  background-color: var(--moderate-cyan);
  border-radius: 50px;
  padding: 20px 30px;
}

.button-smaller {
  padding-inline: 40px;
  font-weight: var(--font-bold);
  font-size: 1rem;
  color: white;
  cursor: pointer;
  transition: background 100ms ease-in-out;
}

.button-smaller:hover {
  background: var(--dark-cyan);
}

nav {
  height: 550px;
  background: url(../../../images/image-hero-desktop.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  padding-left: 200px;
  padding-right: 200px;
  padding-top: 50px;
}
nav .nav__container {
  display: flex;
  justify-content: space-between;
}
nav .nav__container ul {
  display: flex;
  list-style: none;
}
nav .nav__container ul li a {
  color: white;
  margin-left: 2rem;
}

.starter {
  border: solid var(--gray) 1px;
  margin-bottom: 30px;
  border-radius: 10px;
  background: white;
  padding: 50px 60px;
  text-align: center;
}
.starter .logo {
  position: absolute;
  top: -28px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
}
.starter h2 {
  font-size: 2.25rem;
  color: var(--black);
}
.starter p {
  margin-bottom: 40px;
  letter-spacing: 0.05rem;
}
.starter div {
  display: flex;
  justify-content: space-between;
}
.starter .button {
  height: 56px;
  font-weight: var(--font-bold);
  font-size: 1.1rem;
  letter-spacing: 0.05rem;
  color: white;
  cursor: pointer;
  transition: background 100ms ease-in-out;
}
.starter .button:hover {
  background-color: var(--dark-cyan);
}
.starter .bookmark {
  height: 56px;
  padding-left: 0;
  padding-right: 25px;
  text-align: center;
  background-color: var(--gray);
  display: flex;
  align-items: center;
  font-weight: var(--font-bold);
  font-size: 1.1rem;
  color: var(--dark-gray);
  cursor: pointer;
  transition: opacity 100ms ease-in-out;
}
.starter .bookmark img {
  margin-right: 10px;
}
.starter .bookmark:hover {
  opacity: 75%;
}
.starter .bookmarked {
  color: var(--dark-cyan);
}

.stats {
  border: solid var(--gray) 1px;
  margin-bottom: 30px;
  border-radius: 10px;
  background: white;
  padding: 50px 60px;
}

.stats .all_stats_container {
  display: flex;
}
.stats .all_stats_container .stats_container:nth-child(1) {
  padding-right: 80px;
}
.stats .all_stats_container .stats_container:nth-child(2), .stats .all_stats_container .stats_container:nth-child(3) {
  padding-inline: 80px;
}
.stats .all_stats_container .stats_container:nth-child(1), .stats .all_stats_container .stats_container:nth-child(2) {
  border-right: 1px solid var(--gray);
}
.stats .all_stats_container .stats_container h2 {
  font-size: 2.25rem;
  color: var(--black);
}
.stats .all_stats_container .stats_container p {
  letter-spacing: 0;
}
.stats .stats-bar {
  width: auto;
  height: 10px;
  margin-top: 30px;
  background-color: var(--gray);
  border-radius: 50px;
}
.stats .stats-bar .inner-bar {
  width: 80%;
  height: inherit;
  background-color: var(--moderate-cyan);
  border-radius: 50px;
}

.about {
  border: solid var(--gray) 1px;
  margin-bottom: 30px;
  border-radius: 10px;
  background: white;
  padding: 50px 60px;
}
.about h3 {
  font-size: 1.3rem;
  font-weight: var(--font-bold);
  margin-bottom: 40px;
}
.about p {
  margin-bottom: 35px;
  letter-spacing: 0.025rem;
}
.about .card {
  border: solid var(--gray) 1px;
  margin-bottom: 30px;
  border-radius: 10px;
  background: white;
  padding: 50px 60px;
}
.about .card .headline {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.about .card .headline h3,
.about .card .headline p {
  margin-bottom: 25px;
  padding: 0;
}
.about .card .headline h3 {
  font-size: 1.3rem;
  font-weight: var(--font-bold);
}
.about .card .headline p {
  color: var(--moderate-cyan);
  opacity: 100%;
}
.about .card .footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.about .card .footer .p-container {
  display: flex;
  align-items: center;
}
.about .card .footer .p-container .numbers-left {
  margin: 0;
  margin-right: 7px;
  font-size: 2rem;
  color: var(--black);
  opacity: 100%;
  font-weight: var(--font-bold);
}
.about .card .footer .p-container .smaller {
  margin: 0;
  font-size: 1rem;
}

.gray {
  opacity: 50%;
}

.button-gray {
  background-color: var(--dark-gray);
  padding-inline: 40px;
  font-weight: var(--font-bold);
  font-size: 1rem;
  color: white;
}

.close {
  display: none;
}

.back-project-popup {
  border: solid var(--gray) 1px;
  margin-bottom: 30px;
  border-radius: 10px;
  background: white;
  padding: 50px 60px;
  max-width: 925px;
  position: absolute;
  z-index: 10;
  right: 0;
  left: 0;
  margin-inline: auto;
  margin-bottom: 0;
}
.back-project-popup .close-popup {
  background-image: url("../../../images/icon-close-modal.svg");
  width: 15px;
  height: 15px;
  position: absolute;
  right: 40px;
  top: 40px;
  cursor: pointer;
  transition: background 100ms ease-in-out;
}
.back-project-popup .close-popup:hover {
  background-image: url("../../../images/icon-close-modal-hover.svg");
}
.back-project-popup h3 {
  font-size: 1.5rem;
  font-weight: var(--font-bold);
}
.back-project-popup .card-selected {
  border: 2px solid var(--moderate-cyan) !important;
}
.back-project-popup .card {
  border: solid var(--gray) 1px;
  margin-bottom: 30px;
  border-radius: 10px;
  background: white;
  padding: 50px 60px;
  padding: 30px 20px;
  position: relative;
}
.back-project-popup .card .line {
  margin-top: 20px;
  margin-bottom: 30px;
  border-bottom: 1px solid var(--light-gray);
  position: relative;
  left: -20px;
  width: calc(100% + 40px);
}
.back-project-popup .card .payment {
  display: none;
}
.back-project-popup .card .flex {
  display: flex;
}
.back-project-popup .card .no-display {
  display: none;
}
.back-project-popup .card .button-smaller {
  padding: 20px 35px;
}
.back-project-popup .card .pledge-p {
  padding-top: 20px;
  padding-bottom: 0;
  margin: 0;
}
.back-project-popup .card .pledges-container {
  display: flex;
}
.back-project-popup .card .pledges-container :nth-child(2) {
  margin-left: auto;
  margin-right: 10px;
}
.back-project-popup .card .pledge-input {
  -webkit-appearance: none;
  font-weight: var(--font-bold);
  font-size: 1.2rem;
  padding: 0;
  padding-left: 10px;
  padding-right: 5px;
  height: 100%;
  width: 80px;
  border: 1px solid var(--moderate-cyan);
  border-radius: 0px 50px 50px 0px;
  border-left: 0px;
}
.back-project-popup .card .pledge-input:focus {
  border: 1px solid var(--moderate-cyan);
  border-left: 0px;
  outline: none;
}
.back-project-popup .card input[type=text]#dollar {
  height: 100%;
  -webkit-appearance: none !important;
  font-size: 1.2rem;
  color: gray;
  padding-right: 10px;
  text-align: right;
  width: 50px;
  border: 1px solid var(--moderate-cyan);
  border-radius: 50px 0px 0px 50px;
  border-right: 0px;
  margin: 0 -7px 0 0;
  background: white;
}
.back-project-popup .card .radio-container {
  margin-right: 20px;
}
.back-project-popup .card label {
  font-size: 1.1rem;
  font-weight: var(--font-bold);
  cursor: default;
  transition: color 100ms ease-in-out;
}
.back-project-popup .card .reward-label {
  cursor: pointer;
}
.back-project-popup .card .reward-label:hover {
  color: var(--moderate-cyan);
}
.back-project-popup .card input[type=radio] {
  -webkit-appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  outline: none;
  border: 1px solid gray;
  transition: border 100ms ease-in-out;
}
.back-project-popup .card input[type=radio]:before {
  content: "";
  display: block;
  width: 60%;
  height: 60%;
  margin: 20% auto;
  border-radius: 50%;
}
.back-project-popup .card .radio:checked:before {
  background: var(--moderate-cyan);
}
.back-project-popup .card .radio:checked {
  border-color: var(--moderate-cyan);
}
.back-project-popup .card .radio:hover {
  border-color: var(--moderate-cyan);
  cursor: pointer;
}
.back-project-popup .card .radio-button-hovered {
  color: var(--moderate-cyan);
}
.back-project-popup .card .radio-button-clicked {
  color: var(--black);
}
.back-project-popup .card .always-cyan {
  margin-left: 1rem;
  color: var(--moderate-cyan);
}
.back-project-popup .bold {
  font-weight: var(--font-bold);
  font-size: 1.3rem;
  margin-right: 0.25em;
}
.back-project-popup .opacity {
  opacity: 0.5;
}
.back-project-popup .pledges-left {
  position: absolute;
  top: 20px;
  right: 30px;
  opacity: 1;
}
.back-project-popup .p-1 {
  position: absolute;
  right: 90px;
  top: 365px;
  opacity: 1;
}
.back-project-popup .p-2 {
  position: absolute;
  right: 90px;
  top: 565px;
  opacity: 1;
}
.back-project-popup .p-3 {
  position: absolute;
  right: 90px;
  top: 765px;
  opacity: 1;
}

.transparent-background {
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000000;
  opacity: 0.2;
}

.invisible {
  visibility: hidden;
}

body {
  background-color: var(--light-gray);
}

main {
  position: absolute;
  top: 425px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  max-width: 925px;
}